<template>
  <base-facility>
    <template v-slot:title>{{  $t('facility.data.facilitySettingsTitle') }} / {{  $t('facility.data.noSmoking') }}</template>
    <template v-slot:actions>
      <create-room-modal>{{  $t('buttons.createNewRoom') }}</create-room-modal>
    </template>
    <template v-slot:default>
      <v-card class="v-card-responsive-table pa-5" shaped tile >
        <v-card-title class="grey--text mt-n5" v-once>
          <v-breadcrumbs
            large
            :items="breadcrumbItems"
            class="ma-0 pa-0 custom-breadcrumb"
            divider=">"
          ></v-breadcrumbs>
        </v-card-title>
        <v-simple-table class="custom-table d-flex mt-n4">
          <template v-slot:default>
            <thead>
            <tr>
              <th class="text-left">
                {{  $t('facility.roomId') }}
              </th>
              <th class="text-left">
                {{  $t('facility.roomName') }}
              </th>
              <th class="text-left">
                {{  $t('facility.editRoom') }}
              </th>
            </tr>
            </thead>
            <tbody>
            <tr
              v-for="item in rooms"
              :key="item.id"
            >
              <td>{{ item.id }}</td>
              <td class="font-weight-medium">{{ item.name }}</td>
              <td class="text-center">
                <v-btn
                  color="white"
                  elevation="2"
                  x-small
                  fab
                  :to="'/facility/settings/43224/room-types/43323/rooms/' + item.id + '/edit'"
                  target="_blank"
                >
                  <v-icon color="primary">
                    mdi-pencil
                  </v-icon>
                </v-btn>
              </td>
            </tr>
            </tbody>
          </template>
        </v-simple-table>
      </v-card>
    </template>
  </base-facility>
</template>

<script>
import BaseFacility from '@/components/Facilities/BaseFacility'
import CreateRoomModal from '@/components/Facilities/Rooms/CreateModal'

export default {
  name: 'FacilityRoomsSettings',
  data () {
    return {
      rooms: [],
      breadcrumbItems: [
        {
          text: this.$t('facility.data.breadcrumbs.facilityList'),
          disabled: false,
          exact: true,
          to: '/facility/settings'
        },
        {
          text: this.$t('facility.data.breadcrumbs.kyoto'),
          disabled: false,
          exact: true,
          to: '/facility/settings/35598/room-types'
        },
        {
          text: this.$t('facility.data.breadcrumbs.noSmoking'),
          disabled: true
        }
      ]
    }
  },
  mounted () {
    this.initRooms()
  },
  methods: {
    initRooms () {
      for (let i = 0; i < 5; i++) {
        this.rooms.push({
          id: Math.floor(Math.random() * 90000) + 10000,
          name: '10' + (i + 1)
        })
      }
    }
  },
  components: {
    BaseFacility,
    CreateRoomModal
  }
}
</script>

<style scoped lang="scss">

</style>
